import "./styles.css";
import { useState } from "react";
export default function App() {
  const [ans, setAns] = useState([]);
  let movies = {
    Fiction: [
      {
        name: "The Mercury Theatre",
        Rating: "   4/5",
        Desc:
          " --The Mercury Theatre was an independent repertory theatre company founded in New York City in 1937 by Orson Welles and producer John Houseman"
      },
      {
        name: "Avengers: Endgame (2019)",
        Rating: "   5/5",
        Desc:
          " -- It was released in 2019 and it is a superhero movie about the superhero team the Avengers."
      },
      {
        name: "Alien (1979)",
        Rating: "   3.5/5",
        Desc:
          " -- Alien is a 1979 film, where we chronicled as commercial ship called Nostromo, it deviates from its target to land because the central computer called “Mother” has detected a mysterious signal that repeats every 12 seconds when to be a distress signal, crews are obliged to investigate."
      }
    ],
    Family: [
      {
        name: "Harry Potter and the Sorcerer's Stone (2001)",
        Rating: "   4/5",
        Desc:
          " -- The movie begins with a cat sitting on a wall and a man in a beard and robes turning off street lights. The cat turns into Professor McGonagall and the man in the beard is Dumbledore."
      },
      {
        name: "Harry Potter and the Prisoner of Azkaban (2004)",
        Rating: "   5/5",
        Desc:
          " -- Before leaving for Hogwarts, Harry learns from Arthur Weasley that Sirius Black is a convicted murderer from the wizarding world, and has escaped from the wizard prison Azkaban to kill Harry."
      },
      {
        name: "Wish Dragon (2021)",
        Rating: "   5/5",
        Desc: " --  A tale of discovering the greatest wish of all."
      }
    ],
    Romantic: [
      {
        name: "Serendipity (2001)",
        Rating: "   4/5",
        Desc:
          " -- On a magical night when they are in in their 20s, Jonathan (John Cusack) meets Sara (Kate Beckinsale). He finds it love at first sight, but Sara believes in destiny"
      },
      {
        name: " He's Just Not That Into You (2009)",
        Rating: "   3/5",
        Desc:
          "  -- despite the best efforts of a talented cast, He's Just Not That Into You devotes too little time to each of its protagonists, thus reducing them to stereotypes. On Metacritic the film has a weighted average score of 47 out of 100 based on 32 reviews, indicating mixed or average reviews"
      },
      {
        name: " He's All That (2021) ",
        Rating: "   3/5",
        Desc:
          " -- About the movie Adapted from Behrendt and Tuccillo`s best selling book of the same name, the movie focuses on the gap between couples and lack of communication that often leads to misunderstandings in a relationship."
      }
    ],
    Action: [
      {
        name: "Logan (2017)",
        Rating: "   4.5/5",
        Desc:
          " -- ( Hugh Jackman ), formerly the Wolverine, wakes up in the back of a limo, groggy and disheveled. It's the middle of the night. He stumbles out and finds a Mexican gang trying to vandalize the car. "
      },
      {
        name: "  Captain Marvel (2019)",
        Rating: "   3.5/5",
        Desc:
          " -- It is an extraterrestrial Kree warrior who finds herself caught in the middle of an intergalactic battle between her people and the Skrulls."
      },
      {
        name: " Black Widow (2021)",
        Rating: "   5/5",
        Desc:
          " -- Natasha Romanoff aka Black Widow confronts the darker parts of her ledger when a dangerous conspiracy with ties to her past arises."
      }
    ]
  };

  let arrMovie = Object.keys(movies);
  console.log(arrMovie);

  let clickHandler = (movie) => {
    console.log(Object.values(Object.values(movies[movie])));
    let arrOfAns = Object.values(movies[movie]);

    var ans = Object.values(arrOfAns);
    console.log(ans);
    // let meaning = Object.values(movies[movie]);
    setAns(ans);
  };
  return (
    <div className="App">
      <div>
        <h1>Movies-Finder</h1>

        <ul className="genere">
          {arrMovie.map((movie) => {
            console.log(movie);
            return (
              <span
                className="span"
                key={movie}
                onClick={() => clickHandler(movie)}
              >
                {movie}
              </span>
            );
          })}
        </ul>

        <ul className="output">
          {ans.map((movie) => {
            console.log(movie);
            let info = Object.values(movie);
            return (
              <li className="list" key={info}>
                {info}
              </li>
            );
          })}
        </ul>
      </div>
    </div>
  );
}
